.dark-mode.main-content {
  background-color: #1a1a1a;
  .partners {
    background-color: var(--color-dark-grey);
  }
  .fanshop__item,
  .hp-posts__info .h3,
  .tag-grey {
    color: #fff;
  }
}
body .dark-mode {
  color: #fff;
}
.dark-mode-3 {
  
  .header .social {
    padding-left: 0;
    @media (max-width: 1440px) {
    }
  }

  .header {
    background-color: #1a1a1a;
  }
  .scoreboard__item {
    background: #1a1a1a;
  }
  .scoreboard .slick-slide:nth-child(odd) .scoreboard__item {
    background: hsla(0, 0%, 91.8%, 0.4);
  }
  .footer .navbar {
    display: none;
  }
}

.dark-mode-3 .scoreboard__box {
  border: 1px 0.5px solid var(--color-light);
  @media (max-width: 768px) {
    border: none;
  }
}
.scoreboard.scoreboard-new .slick-slide:nth-child(odd) .scoreboard__item {
  background: #1a1a1a;
}
